*{
  font-family: 'dosis', sans-serif;
  letter-spacing: 1.2px;
  line-height: 1.6;
}
#display {
  display: flex;
  min-width: 500px;
  justify-content: center;
  align-items: center;
}
.drum-pad {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 100px;
  border: 1px solid black;
  border-radius: 3px;
}
/*copied from sass files*/
.drum-pad#audio_1 {
  background-color: #ffa2a2;
}

.drum-pad#audio_2 {
  background-color: #ffd1a2;
}

.drum-pad#audio_3 {
  background-color: #ffffa2;
}

.drum-pad#audio_4 {
  background-color: #d1ffa2;
}

.drum-pad#audio_5 {
  background-color: #a2ffa2;
}

.drum-pad#audio_6 {
  background-color: #a2ffd1;
}

.drum-pad#audio_7 {
  background-color: #a2ffff;
}

.drum-pad#audio_8 {
  background-color: #a2d1ff;
}

.drum-pad#audio_9 {
  background-color: #a2a2ff;
}
/*end custom*/


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
